        link: 'https://www.meetup.com/MUCplusplus/events/261889529/',
        description: '<p>On June, 25th, we have the great honour to welcome Mateusz Pusz to our user group. Mateusz is a software architect, chief engineer, and security champion with more than 15 years of experience in designing, writing and maintaining C++ code for fun and living. He is also a contributor and an active voting member of the ISO C++ Committee (WG21) where, together with the best C++ experts in the world, he shapes the future of the C++ language.</p> <p>Abstract:<br/>C++ is the fastest programming language in the world, and it leaves no room for other languages below it (except assembler). That is why it gets more attention now than ever before. As a result, we observe an increasing speed of the C++ language evolution. C++11 was a game changer, but now it is considered an old language already. C++14 and C++17 provided many improvements that allow us to write portable, safer, and faster programs in a shorter time. The resulting source code is easier to reason about and maintain. Moreover, the C++20 is coming soon, and it is going to be a game changer again.</p> <p>In my talk, I provide a few typical legacy code examples that most of us write every day. I describe how those samples evolve with the changes introduced in the subsequent C++ language releases ranging from C++11 up to the major groundbreaking features of upcoming C++20. During the talk, the audience will learn how much easier, more robust, and safer is the code written using modern tools that we get with every new C++ release.</p> <p>Schedule:<br/>19:00 -- Welcome with Pizza and Drinks<br/>19:30 -- Welcome at Austin Fraser<br/>19:40 -- Lightning Talk<br/>19:50 -- Main talk by Mateusz Pusz<br/>~21:00 -- Open Discussions<br/>22:00 -- Official End</p> <p>Sponsor: Austin Fraser</p> ',
